var _this;
import uniLoadMore from "../../components/uni-load-more/uni-load-more.vue"
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			applyHistortArray: [],
			apply_sta: 0,
			apply_type: 2,
			LoadSta: 0, //0默认状态），1加载中），2没有了）
			page: 1,
			limit: 10,
			USERID: '',
			USERROLE: '',
			USERMAIN: '',

		};
	},
	mounted() {
		_this = this;
	},
	onLoad: function(options) {
		_this = this;
		//如果是管理员权限，则直接获取所有申请
		let USERROLE = uni.getStorageSync("USERROLE");
		_this.USERROLE = USERROLE;
	
		let USERID = uni.getStorageSync("USERID");
		_this.USERID = USERID;
	
		let USERMAIN = uni.getStorageSync("USERMAIN");
		_this.USERMAIN = USERMAIN;
		_this.getApplyListBySta();
	},
	onPullDownRefresh() {
		_this.getApplyListBySta();
	},
	onReachBottom() {
		//触底函数
		this.getnewsList();
	},
	onShow() {
		_this.getApplyListBySta();
	},
	methods: {
		openDetail(id, sta, type) {
			if (type == 0) {
				uni.navigateTo({
					url: '/pages/applyCheckInInfo/applyCheckInInfo?id=' + id + '&sta=' + sta+ '&apply_type=' + type
				});
			} else if (type == 1) {
				uni.navigateTo({
					url: '/pages/applyCheckOutInfo/applyCheckOutInfo?id=' + id + '&sta=' + sta+ '&apply_type=' + type
				});
			} else {
				uni.showToast({
					title: '获取申请类型异常，请退出后重试',
					icon: 'none'
				})
			}

		},
		changeAppType(e) {
			_this.LoadSta = 0;
			var type = e.detail.value;
			_this.apply_type = type;
			_this.getApplyListBySta();
		},
		//审批状态条件变更
		changeAppSta(e) {
			_this.LoadSta = 0;
			var sta = e.detail.value;
			_this.apply_sta = sta;
			_this.getApplyListBySta();
		},
		//下拉刷新
		getApplyListBySta() {
			_this.LoadSta = 0;
			_this.page = 1;
			_this.applyHistortArray = [];
			if (_this.USERROLE == 1) {
				var allParams = {
					url: 'queryApplyInfoByLimitAndUserIdWithRoleAdmin',
					setUpUrl: true,
					type: 'get',
					data: {
						user_main: _this.USERMAIN,
						apply_type: _this.apply_type,
						apply_sta: _this.apply_sta,
						page: _this.page,
						limit: _this.limit
					},
					sCallback: function(data) {
						if (data.code == 1) {
							uni.showToast({
								title: data.info,
								icon: 'none'
							})
						} else {
							_this.applyHistortArray = data.data;
							_this.page++;
						}
					}
				};
			} else {
				var allParams = {
					url: 'queryApplyInfoByLimitAndUserIdWithRoleUser',
					setUpUrl: true,
					type: 'get',
					data: {
						apply_type: _this.apply_type,
						apply_sta: _this.apply_sta,
						user_id: _this.USERID,
						page: _this.page,
						limit: _this.limit
					},
					sCallback: function(data) {
						console.log(data);
						if (data.code == 1) {
							uni.showToast({
								title: data.info,
								icon: 'none'
							})
						} else {
							if (data.count == 0) {
								uni.showToast({
									title: data.info,
									icon: 'none'
								});
							}
							_this.applyHistortArray = data.data;
							_this.page++;
						}
					}
				};
			}
			_this.$base.request(allParams, _this);

		},
		//加载更多
		getnewsList() {
			console.log('_this.page=' + _this.page);
			if (_this.LoadSta == 2) {
				uni.showToast({
					title: '没有更多数据了',
					icon: 'none'
				});
				return;
			}
			_this.LoadSta = 1;
			var sta = _this.apply_type;
			var type = _this.apply_sta;
			if (_this.USERROLE == 1) {
				var allParams = {
					url: 'queryApplyInfoByLimitAndUserIdWithRoleAdmin',
					setUpUrl: true,
					type: 'get',
					data: {
						user_main: _this.USERMAIN,
						apply_type: _this.apply_type,
						apply_sta: _this.apply_sta,
						page: _this.page,
						limit: _this.limit
					},
					sCallback: function(data) {
						console.log(data);
						if (data.code == 1) {
							_this.LoadSta = 0;
							uni.showToast({
								title: data.info,
								icon: 'none'
							})
						} else if (data.code == 0) {
							if (data.count == 0) {
								uni.showToast({
									title: '没有更多数据了',
									icon: 'none'
								});
								_this.LoadSta = 2;
								return;
							}
							_this.LoadSta = 0;
							_this.applyHistortArray = _this.applyHistortArray.concat(data.data);
							_this.page++;
						}
					}
				};
			} else {
				var allParams = {
					url: 'queryApplyInfoByLimitAndUserIdWithRoleUser',
					setUpUrl: true,
					type: 'get',
					data: {
						apply_type: _this.apply_type,
						apply_sta: _this.apply_sta,
						user_id: _this.USERID,
						page: _this.page,
						limit: _this.limit
					},
					sCallback: function(data) {
						console.log(data);
						if (data.code == 1) {
							_this.LoadSta = 0;
							uni.showToast({
								title: data.info,
								icon: 'none'
							})
						} else {
							if (data.count == 0) {
								uni.showToast({
									title: '没有更多数据了',
									icon: 'none'
								});
								_this.LoadSta = 2;
								return;
							}
							_this.LoadSta = 0;
							_this.applyHistortArray = _this.applyHistortArray.concat(data.data);
							_this.page++;
							console.log(_this.applyHistortArray);
						}
					}
				};
			}
			_this.$base.request(allParams, _this);
			console.log(_this.applyHistortArray);
		}
	}
}
